.Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

.Hidden {
    display: none;
    transform: translateY(-100vh)
}

.Open {
    transform: translateY(0);
}

.Close {
    transform: translateY(-100%);
}